body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: #d1d8e0;
}

.sidebar{
    background-color: #1e272e !important;
    margin-top: 30px;
    height: 590px;
    margin-bottom: 15px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}

.sidebar .list-group{
    width: 100%;
	overflow: hidden;
	margin-top: 10px;
}

.sidebar .list-group .list-group-item{
	background: none;
	text-align: center;
	border: none;
	list-style: none;
}

.sidebar .list-group .top{
	color:#fff;
	text-transform: uppercase;
	/* font-family: 'Poiret One', cursive; */
	font-family: 'Josefin Sans', sans-serif;
}

.sidebar .list-group .top .name{
	display: block;
	font-size: 15px;
	font-weight: 800;
}

.sidebar .list-group .list-group-item .links{
	display: block;
	color: #fff;
  	text-decoration: none;
  	text-transform: uppercase;
	margin: 0px 15px 5px 15px;
	border: 1px solid #fff;
	padding: 5px;
	font-family: 'IBM Plex Mono', monospace;
}


.sidebar .list-group .list-group-item .links:hover {
  	color: #26de81;
}

.sidebar-footer{
  	margin-top: 40px;
}

.sidebar-footer .list-group-item {
  	border-bottom: none !important;
}

.sidebar-footer .social-icons{
	font-size: 20px;
	color: #fff;
	margin-right: 25px;
}

.sidebar .copy p{
	color: #fff;
	font-size: 10px;
	margin-top: -10px;
}


@media(max-width: 767px)
{
	/* Sidebar section start */

	.sidebar{
		background: none !important;
		height: auto;
		box-shadow: none;
	}

	.sidebar .list-group{
		margin-top: 0;
	}

	.sidebar .list-group .top{
		color:#fff;
		text-transform: uppercase;
		font-size: 15px;
		margin: 6px 6px 6px 10px;
		font-weight: 800;
	}

	.sidebar .list-group .top .name{
		display: inline;
		font-size: 15px;
		font-weight: 800;
	}

	.sidebar .list-group .list-group-item .links
	{
		border: none;
		border-bottom: 1px solid #fff;
		margin: 2px 15px 2px 15px;
	}

	
	.navbar_toggler_icon{
		border: 1px solid red;
		color: #000;
	}

	.navbar-toggler:focus{
		border: none;
	}

	.sidebar .navbar-collapse{
		background-color: #1e272e !important;
		position: absolute;
		margin-top: 5px;
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
		-webkit-animation: load 0.6s forwards;
		        animation: load 0.6s forwards;
		z-index: 2;
	}

	@-webkit-keyframes load {
		0%{
			transform: translateY(0);
		}
		100%{
			opacity: 1;
			transform: translateY(0);
		}
	}

	@keyframes load {
		0%{
			transform: translateY(0);
		}
		100%{
			opacity: 1;
			transform: translateY(0);
		}
	}

	.sidebar-footer{
			margin-top: 5px;
	}

	.sidebar-footer .social-icons{
			font-size: 22px;
			margin-right: 25px;
	}

	/* Sidebar section end */

	
}

