.pageSlider-enter {
	transform: translateY(-100%);
  }
  
  .pageSlider-enter.pageSlider-enter-active {
	transform: translateY(0);
	transition: all 600ms;
  }
  .pageSlider-exit {
	transform: translateY(0);
  }
  
  .pageSlider-exit.pageSlider-exit-active {
	transform: translateY(100%);
	transition: all 600ms;
  }


